body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #21618C;
}

p{
  line-height: 1.6;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

div{
  box-sizing: border-box;
}

header{
  color: white;
}

#instructions{
  font-size: 22px;
  font-weight: bold;
  text-decoration: underline;
}

.hidden{
  visibility: hidden;
}

.display-none{
  display: none;
}

.flex{
  display: -webkit-flex;
  display: flex;
}

.flex-column{
  -webkit-flex-direction: column;
          flex-direction: column;
}

.justify-center{
  -webkit-justify-content: center;
          justify-content: center;
}

.inline-flex{
  display: -webkit-inline-flex;
  display: inline-flex;
}

.hidden{
  visibility: hidden;
}

.btn-container{
  margin-top: 1em; 
}

.btn-container > button{
  margin-right: 1em; 
  cursor: pointer;
}

.btn{
  border: none;
  color: white;
  background-color: #26A69A;
  text-align: center;
  border-radius: 2px;
  padding: 0.5em 1em;
  font-size: 17px;
  font-weight: bold;
}

.btn:hover{
  background-color: #0E6251;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 4em;
  pointer-events: none;
} 

.App-header{
  -webkit-flex-direction: column;
          flex-direction: column; 
  -webkit-align-items: center; 
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;

  margin-bottom: 2em;
}

.App-title{
  font-family: Coiny, cursive;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
} 

.Game{
  background: white;
  padding-bottom: 2em;
} 

.divider{
  border: thin solid #00047A;
  display: inline-block;
  width: 100px; 
} 

.move-counter{
  font-size: 20px; 
} 

.game-state{
  font-weight: bold;
  color: green;
} 

.Board{
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.Board-clickable{
  border: 3px dashed transparent;
}

.Board-clickable:hover{
  border: 3px dashed #F39C12;
  cursor: pointer;
}

.Board-answer{
  margin-bottom: 5px;
}

.Board-game{
  border: 5px groove grey;
}

.Board-row > div{
  display: -webkit-flex;
  display: flex;
  min-width: 50px;
  min-height: 50px;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center; 
  font-weight: bold; 
}

.Board-answer-row > div{
  border: thin solid #00047A;
}

.Board-game-row > div{
  border: thin solid grey;
  border-radius: 15%;
}

.white-square{
  color: #00047A;
  border: thin solid #00047A;
}

.blank-square{
  background: -webkit-repeating-linear-gradient(135deg, white, white 5px, #00047A 5px, #00047A 10px);
  background: repeating-linear-gradient(-45deg, white, white 5px, #00047A 5px, #00047A 10px);
}

.red-square{
  background-color: #8E0D00;
  color: #DDDDDD;
}

.beige-square{
  background-color: #FFFF9D;
  color: #222222;
}

.Options-container{
  position: fixed;
  top: 0;
  bottom: 0; 
  width: 100%;
  height: 100%;
  overflow: auto;
  z-index: 1;
  background-color: rgb(0,0,0,0.5);
  padding-top: 100px;
}

.Options-content{
  background-color: white;
  margin: auto;
  padding: 20px;
  width: 70%;
} 

.Options-answer-grids{
  margin-bottom: 1em;
}

.Options-close-btn{
  color: grey;
  font-size: 3rem;
  float: right;
  position: relative;
  bottom: 20px; /* Same as padding for Options-content */
}

.Options-close-btn:hover,
.Options-close-btn:focus {
  color: black;
  cursor: pointer;
}

.Options-answer-grids > div{
  margin-top:auto;
  margin-right: 2em;
}

